'use client'

import Navbar from "../../components/Navbar";
import { useTheme } from "../../context/ThemeContext";
import { FaRegMoon } from "react-icons/fa";
import { BsSun } from "react-icons/bs";
import { FaChevronLeft, FaChevronRight } from "react-icons/fa"; // Ícones de navegação
import { useRef } from "react";
import Course from "../../components/Course";
import arrow from "../../public/images/ComprasIcons/arrow.svg"

export default function Home() {
  const { theme, toggleTheme } = useTheme();
  const scrollRef = useRef<HTMLDivElement>(null);

  const courses = [
    { title: "PRESTAÇÃO DE SERVIÇOS", curr_module: "MÓDULO 12", image: "/images/ComprasIcons/example-1.png" },
    { title: "DROPSHIPPING", curr_module: "MÓDULO 04", image: "/images/ComprasIcons/example-1.png" },
    { title: "GOOGLE ADS", curr_module: "MÓDULO 10", image: "/images/ComprasIcons/example-1.png" },
    { title: "CHAT BOT", curr_module: "MÓDULO 01", image: "/images/ComprasIcons/example-1.png" },
    { title: "SUPORTE & COMUNIDADE", curr_module: "MÓDULO 09", image: "/images/ComprasIcons/example-1.png" },
  ];

  const scroll = (direction: "left" | "right") => {
    if (scrollRef.current) {
      const scrollAmount = 220; // Ajuste conforme necessário
      scrollRef.current.scrollBy({ left: direction === "left" ? -scrollAmount : scrollAmount, behavior: "smooth" });
    }
  };

  return (
    <>
      <div className={`relative w-full min-h-screen flex ${theme === "dark" ? "bg-[#171717] text-white bg-overlay" : "bg-white text-black"}`}>
        <Navbar />

        <div className="ml-[72px] md:ml-[22%] w-full">
          <div className="sm:mx-4 md:mx-8 lg:mx-16">
            <div className="flex justify-between mt-16 mb-4">
              <h2 className={`text-base ${theme === "dark" ? "text-#D9D9D9" : "6C6C6C"}`}>Meus cursos em progresso</h2>

              <div className="mx-4">
                { /* Here will be the arrows to control the slider, use the arrow.svg imported to make these buttons */}
              </div>

            </div>

            <div className="relative">
              {/* Scrollable Container */}
              <div ref={scrollRef} className="flex overflow-x-auto scrollbar-hide space-x-4 pr-0 snap-x scroll-smooth">
                {courses.map((course, index) => (
                  <div key={index} className="snap-start">
                    <Course 
                      title={course.title} 
                      curr_module={course.curr_module} 
                      image={course.image} 
                      content="course-module" 
                    />
                  </div>
                ))}
              </div>
            </div>

          </div>
        

          <button
            onClick={toggleTheme}
            className="fixed bottom-5 right-5 p-3 rounded-full shadow-lg"
          >
            {theme === "dark" ? ( <FaRegMoon className="text-white" size={24} />) : ( <BsSun className="text-[#888888]" size={24} />)}
          </button>

        </div>
      </div>
    </>
  )
}